}
body { font: 12px "\FFFD\FFFD\FFFD\FFFD", Arial, Helvetica, sans-serif; color: #756F71
}
img { border: 0; display: block
}
ul { list-style: none;
}
a:link, a:visited {text-decoration: none; color: #333;
}
.left { float: left;
}
.right { float: right;
}
.blank { height: 5px; overflow: hidden; width: 100%; margin: auto; clear: both
}
.box{ width:1000px; margin:auto; overflow:hidden
}
header { width: 1000px; margin: auto; height: 80px; position: relative; overflow: hidden
}
#logo a { width:310px; height: 60px; margin: 10px 0 0 0; position: absolute; display: block
}
nav { float: right; width: 100%; margin: 30px 0 0 0; text-align: right
}
nav a { position: relative; display: inline-block; font-size: 18px; font-family: "\3A2\FFFD\FFFD\FFFD\17A\FFFD", Arial, Helvetica, sans-serif;
}
nav a:hover { text-decoration: none
}
.topnav a { margin: 0 5px; padding: 0 8px;
}
.topnav a span:first-child { z-index: 2; display: block;
}
.topnav a span:last-child { z-index: 1; display: block; color: #999; font: 12px Georgia, serif; opacity: 0; -webkit-transition: -webkit-transform 0.3s, opacity 0.3s; -webkit-transition: opacity 0.3s, -webkit-transform 0.3s; transition: opacity 0.3s, -webkit-transform 0.3s; transition: transform 0.3s, opacity 0.3s; transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s; -webkit-transform: translateY(-100%); transform: translateY(-100%); text-align: center
}
.topnav a:hover span:last-child, .topnav a:focus span:last-child { opacity: 1; -webkit-transform: translateY(0%); transform: translateY(0%);
}
#topnav_current { color: #e15782;
}
.en { color: #999; font-size: 12px; z-index: 1; display: block;
}
/* ie */
article { width: 1000px; margin: 20px auto; overflow: hidden
}
aside { width: 250px;
}
footer { text-align: center; line-height: 40px; border-top: #E8E8E8 1px solid ; width:1000px; margin:0 auto;
}
footer p span{padding-left:10px;color:#292627;
}
footer p span a{color:#292627;
}
.clear{clear:both;
}
.banner { background: url(../../static/img/b2.85b7c16.png),100% 100% no-repeat;width:1000px;height:345px;margin:0 auto;overflow: hidden;border-radius:5px;position:relative;
}
.texts { width: 350px; line-height: 26px; margin: 60px 0 0 20px; float: left; font-size: 14px;color:#ff0;position:absolute;top:28px;left:97px; font-family: "\3A2\FFFD\FFFD\FFFD\17A\FFFD";
}
.texts .p1{font-size:28px;color:#9F6;
}
.texts .p2{font-size:22px;padding:20px 0px 20px 80px;
}
.texts .p3{font-size:16px;padding-left:180px;color:#fff;
}
.texts p { -webkit-transform: translate(60px); transform: translate(60px);
}
.texts p:nth-child(1) { -webkit-animation: animations 3s ease-out 1s backwards; animation: animations 3s ease-out 1s backwards;
}
.texts p:nth-child(2) { -webkit-animation: animations 3s ease-out 4s backwards; animation: animations 3s ease-out 4s backwards;
}
.texts p:nth-child(3) { -webkit-animation: animations2 5s ease-in-out 7s backwards; animation: animations2 5s ease-in-out 7s backwards;
}
@-webkit-keyframes animations {
0% {-webkit-transform:translate(0);opacity:0;
}
50% {-webkit-transform:translate(30px);opacity:.5;
}
100% {-webkit-transform:translate(60px);opacity:1;
}
}
@keyframes animations {
0% {-webkit-transform:translate(0);transform:translate(0);opacity:0;
}
50% {-webkit-transform:translate(30px);transform:translate(30px);opacity:.5;
}
100% {-webkit-transform:translate(60px);transform:translate(60px);opacity:1;
}
}
@-webkit-keyframes animations2 {
0% {opacity:0;
}
40% {opacity:.8;
}
45% {opacity:.3;
}
50% {opacity:.8;
}
55% {opacity:.3;
}
60% {opacity:.8;
}
100% {opacity:1;
}
}
@keyframes animations2 {
0% {opacity:0;
}
40% {opacity:.8;
}
45% {opacity:.3;
}
50% {opacity:.8;
}
55% {opacity:.3;
}
60% {opacity:.8;
}
100% {opacity:1;
}
}
